.movie {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  padding: 0.25rem;
  border: 1px solid rgba(244, 244, 244, 0.4);
  border-radius: 0.25rem;
}
.movie:hover {
  border: 1px solid rgba(244, 244, 244, 0.8);
}
.movie-image-wrapper {
  flex: 1;
  flex-shrink: 0;
  width: 100%;
  position: relative;
  cursor: pointer;
  margin-bottom: 0.25rem;
}
.movie-img {
  position: absolute;
  width: 100%;
  height: 100%;
  border-top-left-radius: 0.25rem;
  border-top-right-radius: 0.25rem;
  object-fit: fill;
}
.movie-info {
  flex: 0 0 25%;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  /* border: 1px solid red; */
}
.movie-info h3 {
  width: 100%;
  height: 100%;
  /* display: flex; */
  /* justify-content: center; */
  /* align-items: center; */
  text-align: center;
  font-size: 1.1rem;
  padding: 0.25rem;
  padding: 0 0.25rem;
  color: #f4f4f4;
  /* overflow-y: auto; */
  /* flex-wrap: wrap; */
  word-break: break-word;
  /* border: 2px solid yellow; */

  /**/
  display: -webkit-box;
  -webkit-line-clamp: 2; /* 또는 3으로 변경 가능 */
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  line-height: 1.6;
  max-height: 100%;
}

.btn-details {
  flex: 0 0 10%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  color: #f4f4f4;
  cursor: pointer;
}
@media screen and (max-width: 442px) {
  .movie {
    padding: 0.25rem 0.5rem;
  }
}
@media screen and (max-width: 479px) {
  .movie-info {
    flex: 0 0 30%;
    /* flex: 0 0 25%; */
    /**/
    /* flex: 0 0 40px; */
    flex: 0 0 55px;
    flex: 0 0 50px;
  }
  .movie-info h3 {
    font-size: 1rem;
  }
  .btn-details {
    flex: 0 0 30px;
    font-size: 0.9rem;
  }
}

