.section-home::-webkit-scrollbar {
  display: none;
}

.section-home {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  margin-top: 1rem;
  width: 100%;
  /* border: 3px solid red; */
}
.home-wrapper {
  flex: 1;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  /* padding: 0.25rem; */
  padding: 0 0.25rem;
  width: 95%;
  max-width: 100%;
  margin: 0.25rem auto;
  position: relative;
  /* border: 2px solid yellow; */
  /**/
  margin: 0 auto;
}
.prev-index-btn,
.next-index-btn {
  top: 0;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.5rem;
  height: 100%;
  background-color: transparent;
  border-color: transparent;
  opacity: 0.5;
  color: #000;
  transition: all 0.3s linear;
  cursor: pointer;
}

.prev-index-btn {
  left: 0;
}
.next-index-btn {
  right: 0;
}
.prev-index-btn:hover,
.next-index-btn:hover {
  opacity: 1;
}
.preview-wrapper::-webkit-scrollbar {
  width: 8px;
  height: 8px;
  width: 6px;
  height: 6px;
}
.preview-wrapper::-webkit-scrollbar-track {
  border-radius: 4px;
  background-color: #2e2e2e;
}
.preview-wrapper::-webkit-scrollbar-thumb {
  border: 2px solid #f0f0f0;
  background-color: #666;
  border-radius: 6px;
}
.preview-wrapper::-webkit-scrollbar-thumb:hover {
  /* background-color: #555; */
  background-color: #999;
}
.preview-wrapper {
  flex: 0 0 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 0.25rem;
  overflow-x: auto;
  overflow: hidden;
}
@media screen and (max-width: 479px) {
  .section-home {
    margin-top: 0.75rem;

    /**/
    /* margin-top: 1.25rem; */
    margin-top: 1rem;
  }
  .home-wrapper {
    width: 95%;
    max-width: 100%;
    margin: 0.25rem auto;

    /**/
    margin: 0 auto;
    margin-bottom: 0.5rem;
    /* padding: 0 0.25rem; */
  }
  .prev-index-btn,
  .next-index-btn {
    height: 50vh;
    margin-top: 0.25rem;
    min-height: 300px;
    max-height: 400px;
    font-size: 3rem;
    /**/
    height: 40vh;
    min-height: 250px;
    max-height: 400px;
  }
  .prev-index-btn {
    margin-left: 0.25rem;
  }
  .next-index-btn {
    margin-right: 0.25rem;
  }
  .preview-wrapper {
    flex: 0 0 100px;
  }
}

