.section-movie-list::-webkit-scrollbar {
  display: none;
}
.section-movie-list {
  flex: 1;
  display: flex;
  flex-direction: column;
  /* padding: 0.25rem 0; */
  padding: 0;
  /* margin-top: 0.5rem; */
  margin-top: 1rem;
  overflow-y: auto;
  /* border: 3px solid red; */
  /* scrollbar-gutter: stable; */
}

.movie-list-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  /* padding: 0 0.25rem; */
  /* border: 2px solid yellow; */
}
.movie-list-wrapper {
  flex: 1;
  display: grid;
  gap: 0.75rem;
  padding: 0 0.25rem;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  /* border: 3px solid red; */
}
@media screen and (max-width: 479px) {
  .section-movie-list {
    margin-top: 0.75rem;
    /**/
    /* margin-top: 1.25rem; */
    margin-top: 1rem;
  }
  .section-movie-list {
    padding-top: 0.25rem;
    padding-bottom: 0;
  }
  .movie-list-container {
    width: 95%;
    max-width: 100%;
  }
  .movie-list-wrapper {
    /* grid-auto-rows: 260px; */
    /**/
    grid-auto-rows: 240px;
    grid-auto-rows: 250px;
  }
}
